
/* *************** Full menu ***************** */
.menu {
  z-index: 1;
  position: absolute;
  top: 0;
  display: flex;
  color: white;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
    0deg,
    rgba(255, 255, 255, 0) 10%,
    rgb(151, 151, 151) 100%
  );
  height: 100px;
  width: 100%;
  max-width: 100vw;
}
.menu-ul {
  display: flex;
  align-items: center;
}

.menu-list {
  list-style: none;
  line-break: loose;
  padding: 1em 1.5vw;
  cursor: pointer;
  font-weight: 500;
  transition: 0.2s all linear;
  font-size: 0.9em;
  text-shadow: 0px 0px 7px rgba(134, 134, 134, 0.582);
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
    sans-serif;
  white-space: nowrap;
}

.menu-hover-efect:hover {
  background: red;
  text-shadow: none;
}

.menu-image {
  width: 20vw;
  max-width: 200px;
  height: auto;
}

.menu-publish {
  background-color: red;
  border-radius: 5px;
}

.menu-icon-phone{
    font-size: 30px;
    padding: 0 5px 0 0;
    margin: 0;
    vertical-align: -10px;
} 

.menu-icon-profile{
    font-size: 40px;
    padding: 0 -20px 0 20px;
    margin: 0;
    vertical-align: -8px;
}

.menu-link {
  text-decoration: none !important;
  color: white !important;
}


/* **************** mediumMenu ******************** */

.medium-menu{
  height: 86px;
  display: flex;
  flex-flow: nowrap;
  justify-content: space-between;
  background-color: rgb(70, 70, 70);
  color: white;
  align-items: center;
}

.medium-menu-image{
  margin: 15px 10px;
}

.medium-menu-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  
}

.medium-menu-ul{
  position: absolute;
  list-style: none;
}

.medium-menu-publish{
  margin-right: 10px;
}

.medium-menu-burger{
  font-size: 50px;
  margin-right: 10px;
  color: white;
}

.dropdown{
  position: absolute;
  top: 86px;
  width: 200px;
  right: 0;
  border-radius: 0 0 0 8px;
  padding: 1em;
  z-index: 4;
  overflow: hidden;
  background-color: rgb(70, 70, 70);
}

.medium-menu-list{
  background-color: rgb(100, 100, 100);
  margin: 2px;
  border-radius: 4px;
  padding: 10px;
  text-align: center;
}

.medium-menu-icon-phone{
  vertical-align: -4px;
  margin-right: 3px;
  font-size: 18px;
}

/* **************** mediumMenu ******************** */
.mobile-menu{
  display: flex;
  flex-direction: column;
  background-color: rgb(70, 70, 70);
  color: white;
}
.mobile-menu-top{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
}

.mobile-menu-bottom{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  background-color: rgb(44, 44, 44);
}

.mobile-menu-publish{
  list-style: none;
  background-color: red;
  padding: 16px;
  text-align: center;
}

.dropdown-mobile{
  
    position: absolute;
    top: 68px;
    width: 200px;
    right: 0;
    border-radius: 0 0 0 8px;
    padding: 1em;
    z-index: 4;
    overflow: hidden;
    background-color: rgb(70, 70, 70);
}

/* ***** navbar icon ***** */

#nav-icon span:nth-child(1) {
  top: 0px;
}

#nav-icon span:nth-child(2),#nav-icon span:nth-child(3) {
  top: 18px;
}

#nav-icon span:nth-child(4) {
  top: 36px;
}

#nav-icon.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

#nav-icon.open span:nth-child(2) {
  transform: rotate(45deg);
}

#nav-icon.open span:nth-child(3) {
  transform: rotate(-45deg);
}

#nav-icon.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}

#nav-icon{
  width: 60px;
  height: 45px;
  position: relative;
  margin: 0 10px;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  margin-top: 7px;
}

#nav-icon span{
  display: block;
  position: absolute;
  height: 3px;
  width: 90%;
  background: white;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}